body,html{height:100%;margin:0;background:#cad2d3;font-family:"Source Sans Pro",sans-serif;color:#3c3c3b}#map{width:100%;height:100vh}#spinner{position:absolute;top:50%;left:50%;z-index:10000;margin-top:-2rem;margin-left:-2rem}.map-legend{position:absolute;bottom:0;left:0;z-index:1000;margin-left:1rem;margin-bottom:2rem;padding:1rem;background-color:#fff;border-radius:0.5rem}.map-legend-row:not(:last-child){margin-bottom:0.75rem}.map-legend-color{width:1rem;height:1rem;margin-right:0.5rem}.flex{display:flex}.flex-column{flex-direction:column}.popup-table{font-size:0.9rem}.popup-table td{padding-right:0.75rem;padding-bottom:0.75rem}.table-wrapper{background:#fff;padding:4rem 2rem 2rem 2rem}
